<template lang="pug">
.ui.segment.container.center.aligned#social-posts-container
  h3.ui.header {{ sify('') }}
  .ui.three.stackable.cards(v-if="posts.length > 0")
    .ui.card(v-for="post in posts" :key="post.id")
      .content
        .header
          a(:href="`https://g0v.social/@${post.account?.username}`" target="_blank" rel="noopener noreferrer")
            img.ui.avatar.image(:src="post.account.avatar" :alt="post.account.display_name || post.account.username || ''")
            | {{ post.account.display_name || post.account.username || '' }}
        .meta
          span.date {{ formatDate(post.created_at) }}
        .description
          p(v-html="truncateContentAndEscape(post.content)")
        .extra
          a.ui.basic.button(:href="`https://g0v.social/@${post.account?.username}/${post.id}`" target="_blank" rel="noopener noreferrer")
            i.external.alternate.icon
            | {{ sify('') }}
      .extra.content(v-if="post.media_attachments && post.media_attachments.length > 0")
        img.ui.fluid.image(:src="post.media_attachments[0].preview_url" :alt="post.media_attachments[0].description || ''")
  .ui.message(v-else-if="!loading")
    .content
      p {{ sify('') }}
  .ui.loader(v-if="loading")
</template>

<script>
import { sify } from 'chinese-conv'

export default {
  name: 'HomeSocialPosts',
  props: ['si'],
  data() {
    return {
      posts: [],
      loading: true
    }
  },
  methods: {
    sify(t) {
      if (this.si) {
        return sify(t)
      } else {
        return t
      }
    },
    async fetchPosts() {
      try {
        this.loading = true
        //  g0v.social  API  # 
        const response = await this.$http.get('https://g0v.social/api/v1/timelines/tag/?limit=3', {
          params: {
            local: true,
            limit: 40
          },
          timeout: 10000, // 10
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        })
        
        //  # 
        const filteredPosts = response.data.filter(post => {
          return post.content && (post.content.includes('#') || post.content.includes(''))
        }).slice(0, 3)
        
        this.posts = filteredPosts
      } catch (error) {
        console.error('Error fetching social posts:', error)
        //  API 
        this.posts = []
      } finally {
        this.loading = false
      }
    },
    formatDate(dateString) {
      const date = new Date(dateString)
      return date.toLocaleDateString('zh-TW', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      })
    },
    truncateContentAndEscape(content) {
      //  HTML 
      const cleanContent = content.replace(/<[^>]*>/g, '')
      if (cleanContent.length > 100) {
        return cleanContent.substring(0, 100) + '...'
      }
      return cleanContent
    }
  },
  mounted() {
    this.fetchPosts()
  }
}
</script>

<style scoped>
#social-posts-container {
  margin: 2em auto;
  max-width: 1200px;
}

.ui.card {
  margin: 1em;
}

.ui.card .content .description {
  margin-top: 1em;
  line-height: 1.6;
}

.ui.card .extra.content img {
  max-height: 200px;
  object-fit: cover;
}

.ui.loader {
  margin: 2em auto;
}
</style> 