<template>
  <div class="ai-container">
    <h2 class="ui left aligned header">
      <i class="ui question circle icon"></i>
      AI 
      <div class="left aligned sub header">
        AI
      </div>
    </h2>
    <div class="input-section">
      <textarea
        v-model="userQuestion"
        placeholder="..."
        rows="4"
        class="question-input"
      ></textarea>

      <div class="ui vertical buttons">
        <!--  -->
        <button class="ui basic button" @click="setExampleQuestion('')"></button>
        <button class="ui basic button" @click="setExampleQuestion('')"></button>
        <button class="ui basic button" @click="setExampleQuestion('1518')">1518</button>
        <button class="ui green button" @click="askQuestion" :disabled="isLoading">
          {{ isLoading ? '...' : '' }}
        </button>
      </div>
    </div>
    
    <div class="response-section" v-if="response">
      <div class="response-content" v-html="formattedResponse"></div>
    </div>
  </div>
</template>

<script>
import { marked } from 'marked'

export default {
  name: 'AI',
  mounted() {
    //  MathJax
    const script = document.createElement('script')
    script.src = 'https://cdn.jsdelivr.net/npm/mathjax@3/es5/tex-mml-chtml.js'
    script.async = true
    document.head.appendChild(script)
    
    //  MathJax
    window.MathJax = {
      tex: {
        inlineMath: [['$', '$']],
        displayMath: [['$$', '$$']]
      }
    }
  },
  data() {
    return {
      userQuestion: '',
      response: '',
      isLoading: false
    }
  },
  computed: {
    formattedResponse() {
      if (!this.response) return ''
      console.log(this.response)
      
      //  LaTeX  \( \)  $$ $$
      let processedText = this.response
        .replace(/\\\(/g, '$$')
        .replace(/\\\)/g, '$$')
        .replace(/\\\[/g, '$$')
        .replace(/\\\]/g, '$$')
      
      //  markdown
      const html = marked((processedText))
      
      //  tick 
      this.$nextTick(() => {
        if (window.MathJax) {
          window.MathJax.typesetPromise()
        }
      })
      
      return html
    }
  },
  methods: {
    setExampleQuestion(question) {
      this.userQuestion = question
    },
    async askQuestion() {
      if (!this.userQuestion.trim()) return
      
      this.isLoading = true
      this.response = ''
      
      try {
        const response = await fetch('https://freemath-backend.alearn13994229.workers.dev/api/ai_tutor', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            question: this.userQuestion
          })
        })

        const data = await response.json()
        console.log(data)
        
        if (data.response) {
          this.response = data.response
        } else {
          this.response = ''
        }
      } catch (error) {
        this.response = ''
        console.error('Error:', error)
      } finally {
        this.isLoading = false
      }
    }
  }
}
</script>

