<template lang="pug">
.sub.header#main-tip
  a(@click="prevTip")
    i.chevron.left.icon
  .filler
  .tip
    span(:key="randomTip" v-html="sify(randomTip)")
  .filler
  a(@click="nextTip")
    i.chevron.right.icon
</template>


<script>
import { sify } from 'chinese-conv'

export default {
  name: 'Tips',
  props: ['si'],
  data() {
    return {
      tips: [
        '',
        '151',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        'Google',
        'PDF',
        '',
        '',
        '<a href="https://docs.google.com/document/d/14j0lEEZH5A1FNum7L6p9U1ETWx_0JTyasahIi9DLe3w/" target="_blank" rel="noopener norefferer"></a>',
        '<a href="https://www.youtube.com/watch?v=Hnmtv4TKBtc&t=254s" target="_blank" rel="noopener norefferer"></a>'
      ],
      randomTip: ''
    }
  },
  mounted() {
    this.interval = setInterval(this.nextTip, 5000) // 
  },
  beforeDestroy() {
    clearInterval(this.interval) // 
  },
  methods: {
    sify(t) {
      return this.si ? sify(t) : t
    },
    getRandomTip() {
      const randomIndex = Math.floor(Math.random() * this.tips.length)
      this.randomTip = this.tips[randomIndex]
    },
    prevTip() {
      let currentIndex = this.tips.indexOf(this.randomTip)
      currentIndex = (currentIndex - 1 + this.tips.length) % this.tips.length
      this.randomTip = this.tips[currentIndex]
    },
    nextTip() {
      let currentIndex = this.tips.indexOf(this.randomTip)
      currentIndex = (currentIndex + 1) % this.tips.length
      this.randomTip = this.tips[currentIndex]
    }
  },
  created() {
    this.getRandomTip()
  }
}
</script>

