}

a {
  display: inline;
}

.tip {
  flex-grow: 1;
  overflow: hidden; /*  */
  position: relative;
  white-space: nowrap; /*  */
}

.tip span {
  display: inline-block;
  padding-left: 100%; /*  */
  animation: marquee 10s linear forwards; /* 5 */
}

@keyframes marquee {
  0% { transform: translateX(0); }
  100% { transform: translateX(-100%); }
}

.filler {
  flex-grow: 1;
}
</style>
